import ChekoutPage from './Pages/Checkout.page';
import HomePage from './Pages/Home.page';
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";

const router = createBrowserRouter([
  {
    path: "/",
    element: <HomePage />,
  },
  {
    path: "checkout/:order_id",
    element: <ChekoutPage />
  }
]);

function App () {
 
  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;