import React, { useEffect, useState } from 'react';
import { CompassOutlined, UserOutlined } from '@ant-design/icons';
import { Card, Badge, Button, Form, Input, Modal, notification } from 'antd';
import { Order } from '../dto/Order';


function UserInfoComponent(props: any) {
    if (props.userInfo.clientName && props.userInfo.clientAddress && !props.openAddress) {
        return (
            <div>
                <p>Name: {props.userInfo.clientName}</p>
                <p>Address: {props.userInfo.clientName}</p>
            </div>
        );
    }
    return (
        <Form
            name="user_info"
            className="user-form"
            onFinish={props.onFinish}
        >
            <Form.Item
                name="client_name"
                rules={[{ required: true, message: 'Please input your name!' }]}
            >
                <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Name" />
            </Form.Item>

            <Form.Item
                name="client_address"
                rules={[{ required: true, message: 'Please input your Address!' }]}
            >
                <Input
                prefix={<CompassOutlined className="site-form-item-icon" />}
                placeholder="Address"
                />
            </Form.Item>
        
            <Form.Item>
                <Button type="primary" htmlType="submit" className="login-form-button">
                Ekle
                </Button>
            </Form.Item>
        </Form>
    );
}

function PaymentSimilation({ isModalOpen, order, handleOk }: { handleOk: Function, isModalOpen: boolean, order: Order }) {
    async function PaymentService() {
        const order_id = order.Id;
        const token = 'Trk5Ldkm4Dmal10Gmllsk+58sld';

        await handleOk(order_id, token);
    }

    return (
        <Modal title="Basic Modal" open={isModalOpen} onOk={PaymentService}>
            <p>Kaytl kartnla demeyi tamamla.</p>
        </Modal>
    )
}

export default function OrderComponent({ order, OpenNotification }: { order: Order, OpenNotification: (msgType: 'success'|'error', message: string)=>void }) {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [openAddress, setOpenAddress] = useState(false);
    const [userInfo, setUserInfo] = useState({
        clientAddress: '',
        clientName: ''
    });

    const showModal = () => {
        setIsModalOpen(true);
    };

    const handleOk = async (order_id:string, token:string) => {
        setIsModalOpen(false);
        const result = await ComplatePayment(order_id, token);
    };

    async function CreateOrder(value: any) {
        const order_id = order?.Id;

        const rawResponse = await fetch('http://localhost:3000/orders/addclient', {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ order_id, ...value }),
        });

        const result = await rawResponse.json();

        if (result.data) {
            setUserInfo({
                clientName: value.client_name,
                clientAddress: value.client_address,
            });
            OpenNotification('success', result.message);
        } else {
            OpenNotification('error', result.message);
        }
    }

    async function ComplatePayment(order_id:string, token:string) {
        const rawResponse = await fetch(`http://localhost:3000/orders/payment/${order_id}/${token}`);

        const result = await rawResponse.json();

        if (result.data?.id) {
            OpenNotification('success', result.message);
        } else {
            OpenNotification('error', result.message);
        }
    }

    const onFinish = async (value: any) => {
        await CreateOrder(value);
        setOpenAddress(false);
    };

    useEffect(() => {
        console.log(order)
        if (order?.ClientName && order?.ClientAddress) {
            console.log('buraya giridm')
            setUserInfo({
                clientAddress: order?.ClientName,
                clientName: order.ClientAddress
            });
        }
    }, []);
    
    return (
        <Badge.Ribbon text={order.Id}>
            <Card 
                title="Sipari Detay"
                actions={[
                    <Button type="primary" disabled={!!!userInfo.clientName || openAddress} onClick={showModal}>Checkout</Button>,
                ]}
            >
                rnler:
                {
                    order.OrderItems.map((o, index) => <p key={index}>{o.Name}: {o.Price}</p>)
                }

                <p>Total Product Price: {order.TotalProductValue}</p>
                <p>Total Shipment Price: {order.TotalShippingValue}</p>
                
                <UserInfoComponent openAddress={openAddress} userInfo={userInfo} onFinish={onFinish} />
                {openAddress ? <></> : <Button onClick={()=>setOpenAddress(true)}>Update Info</Button>}

                <PaymentSimilation order={order} isModalOpen={isModalOpen} handleOk={handleOk} />
            </Card>
        </Badge.Ribbon>
    )
}