import React, { useEffect, useState } from 'react';
import { Breadcrumb, Layout, notification, Row, Col, Space } from 'antd';
import { useParams } from 'react-router-dom';
import OrderComponent from '../Components/Order.component';

const { Header, Content, Footer } = Layout;

export default function ChekoutPage() {
    const [api, contextHolder] = notification.useNotification();
    let { order_id } = useParams();
    const [order, setOrder] = useState(null);

    useEffect(() => {
        async function getOrder() {
            const response = await fetch(`http://localhost:3000/orders/get/${order_id}`);
            const obj = await response.json();
            setOrder(obj.data);
        }
        getOrder();
    }, []);

    function OpenNotification (msgType: 'success'|'error', message: string) {
        api[msgType]({
            message: `Notification ${msgType}`,
            description: message,
            placement: "topLeft",
        });
    };

    return (
        <Layout>
            {contextHolder}
            <Content className="site-layout" style={{ padding: '0 50px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                <Breadcrumb.Item>Checkout</Breadcrumb.Item>
                </Breadcrumb>
                
                <Row>
                <Col xs={{ span: 0, offset: 1 }} lg={{ span: 2, offset: 2 }} style={{ "marginTop": 20 }} />
                    <Col xs={{ span: 21, offset: 1 }} lg={{ span: 14, offset: 2 }} style={{ "marginTop": 20 }}>
                        {
                            order ?  <OrderComponent OpenNotification={OpenNotification} order={order} /> : <>Loading</>
                        }
                       
                    </Col>
                <Col xs={{ span: 0, offset: 1 }} lg={{ span: 2, offset: 2 }} style={{ "marginTop": 20 }} />
                </Row>

            </Content>
            <Footer style={{ textAlign: 'center' }}>Ant Design 2023 Created by Ant UED</Footer>
        </Layout>
    );
}