import React, { useEffect, useState } from 'react';
import { Breadcrumb, Layout, Menu, Row, Col,Space } from 'antd';
import ProductComponent from '../Components/Products.component';
import { Product } from '../dto/Product'

const { Header, Content, Footer } = Layout;

export default function HomePage() {
    const [products, setProducts] = useState(new Array<Product>());

    useEffect(() => {
        async function getProducts() {
            const response = await fetch('http://localhost:3000/products/find/');
            const obj = await response.json();
            setProducts(obj.data);
        }
        getProducts();
    }, [products[0]?.Id])

    return (
        <Layout>
            <Header
                style={{
                position: 'sticky',
                top: 0,
                zIndex: 1,
                width: '100%',
                display: 'flex',
                alignItems: 'center',
                }}
            >
                <div className="demo-logo" />
                <Menu
                    theme="dark"
                    defaultSelectedKeys={['Market']}
                    items={[
                        {
                            key: "Market",
                            label: `Market`,
                        },
                    ]}
                />
            </Header>
            <Content className="site-layout" style={{ padding: '0 50px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                <Breadcrumb.Item>Home</Breadcrumb.Item>
                </Breadcrumb>
                
                <Row>

                    {
                        products.map((p, index) => {
                            return (
                                <Col xs={{ span: 5, offset: 1 }} lg={{ span: 6, offset: 2 }} style={{ "marginTop": 20}}>
                                <ProductComponent key={index} product={p} />
                                </Col>
                            )
                        })
                    }
                </Row>

            </Content>
            <Footer style={{ textAlign: 'center' }}>Ant Design 2023 Created by Ant UED</Footer>
        </Layout>
    );
}